<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="70%"
    direction="btt"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar>
      <div style="margin: 0 10px;">
        <divider>物资检验</divider>
        <el-row>
          <el-descriptions :border="true" direction="vertical" :column="8">
            <el-descriptions-item label="合同号">{{ dialogForm.contractNo }}</el-descriptions-item>
            <el-descriptions-item label="订单号">{{ dialogForm.orderNo }}</el-descriptions-item>
            <el-descriptions-item label="供应商">{{ dialogForm.supplierName }}</el-descriptions-item>
            <el-descriptions-item label="交接单号">{{ dialogForm.no }}</el-descriptions-item>
            <el-descriptions-item label="交货日期">{{ dialogForm.deliveryDate }}</el-descriptions-item>
            <el-descriptions-item label="检验单号">{{ dialogForm.inspectNo }}</el-descriptions-item>
            <!-- 检验单状态 -->
            <el-descriptions-item label="状态">
              <el-tag
                :type="statusMap[dialogForm.status]"
                disable-transitions
              >
                {{ statusChartMap[dialogForm.status] }}
              </el-tag>
            </el-descriptions-item>
          </el-descriptions>
        </el-row>

        <el-row>
          <table-com
            :key="refreshKey"
            ref="baseTableRef"
            :loading="loading"
            :index="true"
            :selection="false"
            :table-data="tableData"
            :column-list="columnList"
            :table-height-type="360"
            :operation-width="130"
            :expand-list="expandList"
            :table-column-sort="false"
            @rowClick="handleRowClick"
            @refresh="searchInit"
          >
            <template slot-scope="scope">
              <el-button type="primary" @click="handleModify(scope.row)">详细</el-button>
            </template>
          </table-com>
        </el-row>
        <pagination
          :page.sync="pageInfo.current"
          :limit.sync="pageInfo.size"
          :total="total"
          @pagination="init"
        />
      </div>
    </el-scrollbar>
    <!-- <div class="drawer-footer clearfix">
      <el-button class="fr" @click="beforeClose">取 消</el-button>
      <el-button :loading="loading" type="primary" class="fr" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
    </div> -->

    <!-- 新增 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogInfo"
      :edit-type="editInfoType"
      :title="'查看不合格条码详细内容'"
    />

  </el-drawer>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './DetailDialog'
import { getDetailListApi as getListApi } from '@/apis/modules/in-storage/inspection'
import { statusMap, statusChartMap, statusMap1, statusChartMap1 } from '@/views/in-storage/inspection/status'

const INIT_FORM = {
  code: '',
  supplierId: '',
  supplierName: '',
  contact: '',
  phone: '',
  purchaseDate: ''
}

export default {
  name: 'InStorageConsignCom',
  components: {
    EditDialog
  },
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM }
      }
    }
  },
  data() {
    return {
      title: '检验详细内容',
      refreshKey: new Date().getTime(),
      form: INIT_FORM,
      loading: false,
      rules: {},
      query: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编号',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '规格型号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'deliveryNumber',
          label: '交货数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'applicant',
          label: '申请人',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'qualifiedNumber',
          label: '合格数量',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (row) => {
            return row.deliveryNumber - row.unqualifiedNumber
          }
        },
        {
          module: 'word',
          prop: 'unqualifiedNumber',
          label: '不合格数量',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'status',
          label: '状态',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (status) => {
            return this.statusChartMap1[status]
          },
          statusFormatter: (row) => {
            return this.statusMap1[row.status]
          }
        }
      ],
      expandList: [],
      dialogVisible: false,
      dialogInfo: {},
      editInfoType: 'add',
      statusMap,
      statusChartMap,
      statusChartMap1,
      statusMap1
    }
  },
  computed: {
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          page: {
            ...this.pageInfo
          },
          ...this.query,
          deliveryReceiptId: this.dialogForm.id
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleConfirm(formName) {
      this.beforeClose()
    },
    // 删除
    handleDelete(item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        this.tableData = this.tableData.filter(i => i.uuid !== item.uuid)
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: `已取消删除`
        })
      })
    },
    onSelectConfirm(list) {
      if (list.length) {
        this.form.supplierId = list[0].id
        this.form.supplierName = list[0].name
      }
    },
    handleAdd() {
      this.editInfoType = 'add'
      this.dialogInfo = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogInfo = { ...item }
      this.editInfoType = 'modify'
      this.dialogVisible = true
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      this.init()
    },
    initSelect() {
      this.init()
    },
    beforeClose(done) {
      // this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM })
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
  width: 860px;
  margin: 0 auto;
  .left{
    width: 640px;
  }
  .right{
    margin-left: 20px;
    width: 180px;
  }
  }
  ::v-deep:focus {
    outline: 0;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  ::v-deep .el-drawer__header {
    margin-bottom: 10px !important;
  }

  .drawer-footer{
    margin-top: 30px;
    margin-right: 30px;
  }
  </style>

