<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="supplier" class="fl">
          <el-input v-model.trim="query.supplier" clearable placeholder="供应商编号或名称" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="code" class="fl">
          <el-input v-model.trim="query.code" clearable placeholder="合同号或订单号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="status" class="fl">
          <el-select v-model="query.status" filterable clearable placeholder="单据状态" style="width: 178px;" class="filter-item">
            <el-option
              v-for="item in deliveryReceiptStatusPullMap"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item prop="no" class="fl">
          <el-input v-model.trim="query.no" clearable placeholder="交接单号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="inspectNo" class="fl">
          <el-input v-model.trim="query.inspectNo" clearable placeholder="检验单号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="startDate" class="fl">
          <el-date-picker
            v-model="query.startDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="开始时间"
            @change="query.endDate = ''"
          />
        </el-form-item>
        <el-form-item prop="endDate" class="fl">
          <el-date-picker
            v-model="query.endDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="结束时间"
            :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startDate).getTime()}"
          />
        </el-form-item>
        <!-- <el-form-item prop="name" class="fl">
          <el-input v-model.trim="query.name" placeholder="入库状态" class="filter-item" style="width: 178px;" />
        </el-form-item> -->
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="180"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" :disabled="scope.row.status >= 3" @click="toDetail(scope.row)">检验</el-button>
          <el-button type="primary" @click="handleDocument(scope.row)">文档管理</el-button>
          <!-- <el-button type="success" :disabled="scope.row.status !== 2" @click="handleSubmit(scope.row)">完成</el-button> -->
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

    <!-- 物料 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
    />

  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
import { getListApi, submitListApi } from '@/apis/modules/in-storage/inspection'
import { statusMap, statusChartMap, deliveryReceiptStatusPullMap } from '@/views/in-storage/inspection/status'
import tableHeadSet from '@/mixins/tableHeadSet'

export default {
  // name: 'InStorageInspection',
  components: {
    EditDialog
  },
  mixins: [paginationPage, tableHeadSet],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        supplier: '',
        code: '',
        status: '',
        no: '',
        inspectNo: '',
        startDate: '',
        endDate: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'contractNo',
          label: '合同号',
          align: 'center',
          // width: 120,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderNo',
          label: '订单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'supplierName',
          label: '供应商',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'no',
          label: '交接单号',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'deliveryDate',
          label: '交货日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inspectNo',
          label: '检验单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'status',
          label: '状态',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (status) => {
            return statusChartMap[status]
          },
          statusFormatter: (row) => {
            return statusMap[row.status]
          }
        }
      ],
      columnSets: [{
        module: 'status',
        prop: 'status',
        formatter: (status) => {
          return statusChartMap[status]
        },
        statusFormatter: (row) => {
          return statusMap[row.status]
        }
      }],
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false,
      deliveryReceiptStatusPullMap
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = type ? { ...this.query } : {
        ...this.query,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 完成
    handleSubmit(item) {
      this.$confirm(`确认检验完成, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        if (!item.id) return
        submitListApi({
          id: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '操作成功',
              type: 'success'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$message({
          message: '已取消操作',
          type: 'info'
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
      this.handleModify({ ...row })
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    toDetail(item) {
      this.$router.push({
        name: 'InStorageInspectionDetail',
        params: {
          id: item.id
        }
      })
    },
    handleDocument(item) {
      this.$router.push({
        name: 'InStorageInspectionDocument',
        params: {
          id: item.id
        }
      })
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
