<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="1100px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <el-row>
        <el-descriptions :border="true" direction="vertical" :column="4">
          <el-descriptions-item label="物料编号">{{ dialogForm.materialCode }}</el-descriptions-item>
          <el-descriptions-item label="物料名称">{{ dialogForm.materialName }}</el-descriptions-item>
          <el-descriptions-item label="规格型号">{{ dialogForm.materialSpec }}</el-descriptions-item>
          <el-descriptions-item label="数量">{{ dialogForm.deliveryNumber }}</el-descriptions-item>
        </el-descriptions>
      </el-row>
      <el-row>
        <table-com
          :ref="refName"
          row-key="uuid"
          :table-height-type="540"
          :loading="loading"
          :index="true"
          :index-fixed="true"
          :selection="false"
          :multiple="multiple"
          :selection-fixed="false"
          :table-column-sort="false"
          :table-data="tableData"
          :column-list="columnList"
          :show-operate="false"
          :operate-fixed="false"
          :table-selection="tableSelection"
          @selectionChange="selectionChange"
        />
      </el-row>
      <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    </el-scrollbar>
  </el-dialog>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
import paginationPage from '@/mixins/paginationPage'
import { getDetailInfoListApi as getListApi } from '@/apis/modules/in-storage/inspection'

export default {
  name: 'MaterialOrderSelect',
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: ''
    },
    // 是否多选，如果多选，返回id的数组， 如果单选返回 {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // 默认勾选的ids
    selection: {
      type: Array,
      default: () => []
    },
    // 多选状态下的最大选择数量
    max: {
      type: Number,
      default: 0
    },
    dialogForm: {
      type: Object,
      default: () => {
        return {}
      }
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      refName: 'modelSelectMaterialOrderSelect',
      form: {

      },
      rules: {},
      query: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'barCode',
          label: '条码号',
          align: 'center',
          // width: 120,
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'status',
          prop: 'inspectResult',
          label: '检验结果',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (status) => {
            if (status === null) {
              return '未检验'
            } else {
              return status ? '合格' : '不合格'
            }
          },
          statusFormatter: (row) => {
            if (row.inspectResult === null) {
              return 'info'
            } else {
              return row.inspectResult ? 'success' : 'danger'
            }
          }
        },
        {
          module: 'word',
          prop: 'inspectNumber',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: (row) => {
            // if (row.id === 0 || !row.id) {
            //   return row.number
            // } else {
            //   if (row.status === null) {
            //     // 没有结果时候 直接显示数量
            //     return row.inspectNumber
            //   } else {
            //     // 有检验结果的时候 合格+让步
            //     return row.qualifiedNumber + row.compromiseNumber
            //   }
            // }
            return row.number
          },
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'remarks',
          label: '不合格原因',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      tableSelection: []
    }
  },
  methods: {
    confirmDialog() {
      // this.$emit('confirm', this.tableSelection)
      this.beforeClose()
    },
    init(type) {
      const params = type
        ? {
          deliveryMaterialId: this.dialogForm.id,
          ...this.extraInfo
        } : {
          deliveryMaterialId: this.dialogForm.id,
          ...this.extraInfo,
          page: {
            ...this.pageInfo
          }
        }
      return new Promise((resolve, reject) => {
        this.loading = true
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list.map(item => {
              return {
                ...item,
                uuid: uuidv4()
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initSelect() {
      this.init()
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 更新列表复选框勾选
    handleUpdateTableSelected(type = '') {

    },
    refresh() {
      this.init()
    },
    beforeClose(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
    },
    onDeptSelectConfirm(list) {
      if (list.length) {
        this.query.usedOffice = list[0].name
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 700px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
